import { Component, Input } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { MyPageArticoloGiornoRS, PrezzoGiornoMySelf } from 'src/app/shared/models/dto';
import { UtilityService } from 'src/app/shared/services/utility.service';

import { BaseComponent } from '../base-component/base-component';

@Component({
  selector: 'app-giorni-select',
  templateUrl: './giorni-select.component.html',
  styleUrls: ['./giorni-select.component.scss']
})
export class GiorniSelectComponent extends BaseComponent {

  @Input() giorni: MyPageArticoloGiornoRS[] | PrezzoGiornoMySelf[];
  @Input() dialogRef: MatDialogRef<{}, any>;

  constructor(public utility: UtilityService) {
    super();
  }

  checkGiornoDisabled(selected: boolean): boolean {
    return this.giorni.filter(x => x.selected).length <= 1 && selected;
  }
}
