import { Component, EventEmitter, Input, Output } from '@angular/core';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import {
    TipoCameraTariffaDetailsDialogComponent,
} from 'src/app/interfaces/tipo-camera-tariffa-details-dialog/tipo-camera-tariffa-details-dialog.component';
import {
    BookingSolutionRateRS,
    BookingSolutionRoomRS,
    DialogOptions,
    RichiediUpSellingPrenotazioneRQ,
    RichiediUpSellingPrenotazioneRS,
} from 'src/app/shared/models/dto';
import { ApiService } from 'src/app/shared/services/api.service';
import { ContainerService } from 'src/app/shared/services/container.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

@Component({
  selector: 'app-my-self-room',
  templateUrl: './my-self-room.component.html',
  styleUrls: ['./my-self-room.component.scss']
})
export class MySelfRoomComponent extends BaseComponent {

  @Input() oidPrenotazione: number;

  @Input() set _camera(value: BookingSolutionRoomRS) {
    if (value != null) {
      this.camera = value;
      this.tariffa = this.camera.tariffe[0];
      this.immagini = this.camera.immagini.map(x => x.immagine);
      this.descrizioni = this.camera.immagini.map(x => x.descrizione);
    }
  };

  @Output() onRichiedi: EventEmitter<any> = new EventEmitter();

  public camera: BookingSolutionRoomRS;
  public tariffa: BookingSolutionRateRS;
  public immagini: string[] = [];
  public descrizioni: string[] = [];

  constructor(private apiService: ApiService, public utility: UtilityService, private containerService: ContainerService) {
    super();
  }

  richiedi() {
    this.setIsLoading(true);

    let rq = new RichiediUpSellingPrenotazioneRQ();
    rq.oidPrenotazione = this.oidPrenotazione;
    rq.oidTipoCamera = this.camera.tipoCameraId;
    rq.prezzo = this.tariffa.prezzoScontato > 0 ? this.tariffa.prezzoScontato : this.tariffa.prezzo;

    this.apiService.richiediUpSellingPrenotazione(rq).subscribe((res: RichiediUpSellingPrenotazioneRS) => {
      this.setIsLoading(false);

      if (res.data.success) {
        this.showInfo('myself.richiestaInviata').then(() => {
          this.onRichiedi.emit();
        });
      } else {
        this.showError('myself.erroreRichiesta');
      }
    });
  }

  openDetails() {
    let dialogOptions = new DialogOptions();

    if (this.containerService.dimension.width >= 992) {
      dialogOptions.width = "50vw";
    } else {
      dialogOptions.width = "80vw";
    }

    dialogOptions.panelClass = 'dialog-overlay';

    let ref = this.showModalDialog(TipoCameraTariffaDetailsDialogComponent, dialogOptions);
    let component = <TipoCameraTariffaDetailsDialogComponent>ref.componentInstance;
    component.tipoCamera = this.camera;
    component.tariffa = this.tariffa;
    component.isVisualizzaDettaglioGiornaliero = false;
  }
}