import { Component, Input } from '@angular/core';
import * as moment from 'moment';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { RichiestaMySelf } from 'src/app/shared/models/dto';
import { StatoPreventivo } from 'src/app/shared/models/enum';
import { UtilityService } from 'src/app/shared/services/utility.service';

@Component({
  selector: 'app-my-self-richiesta',
  templateUrl: './my-self-richiesta.component.html',
  styleUrls: ['./my-self-richiesta.component.scss']
})
export class MySelfRichiestaComponent extends BaseComponent {

  @Input() set _richiesta(value: RichiestaMySelf) {
    if (value != null) {
      this.richiesta = value;

      if (this.richiesta != null && !!this.richiesta.dal && !!this.richiesta.al) {
        this.notti = Math.max(moment(this.getDate(this.richiesta.al)).diff(moment(this.getDate(this.richiesta.dal)), 'days'), 1);
      }
    }
  };

  public richiesta: RichiestaMySelf;
  public notti: number = 0;
  public enumStatoPreventivo = StatoPreventivo;

  constructor(private utility: UtilityService) {
    super();
  }

  openMyPage() {
    this.utility.openUrlInNewTab(window.location.origin + '/booking/mypage?uid=' + this.richiesta.uidPreventivo);
  }
}