import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { Subject } from 'rxjs';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { ComponenteXPrenotazione } from 'src/app/shared/models/bo.model';
import { AlbergoMySelf } from 'src/app/shared/models/dto';

@Component({
  selector: 'app-my-self-check-in-multiple',
  templateUrl: './my-self-check-in-multiple.component.html',
  styleUrls: ['./my-self-check-in-multiple.component.scss']
})
export class MySelfCheckInMultipleComponent extends BaseComponent implements OnInit, OnDestroy {

  @Input() ospiti: ComponenteXPrenotazione[];
  @Input() albergo: AlbergoMySelf;
  @Input() isVisualizzaTuttiDatiSelfCheckIn: boolean;
  @Input() isBloccaModificaDatiCheckIn: boolean;
  @Input() myself: boolean = false;
  @Input() lingua: string;
  @Input() checkInIndex: number = 0;
  @Input() orarioDiArrivoMinimo: string;
  @Input() orarioDiArrivoMassimo: string;

  @Output() back: EventEmitter<ComponenteXPrenotazione[]> = new EventEmitter();
  @Output() onStepChange: EventEmitter<any> = new EventEmitter();

  public stepsCompleted: boolean[];
  public saveEnabled: boolean[];
  public saveSubjects: Subject<void>[];

  constructor() { super(); }

  ngOnInit() {
    this.stepsCompleted = this.ospiti.map(() => false);
    this.saveEnabled = this.ospiti.map(() => false);
    this.saveSubjects = this.ospiti.map(() => new Subject());
  }

  onFormValidChange(index: number, formValid: boolean) {
    setTimeout(() => {
      this.stepsCompleted[index] = formValid;
    }, 200);
  }

  onSaveEnabledChange(index: number, saveEnabled: boolean) {
    setTimeout(() => {
      this.saveEnabled[index] = saveEnabled;
    }, 200);
  }

  onCheckIn(index: number, ospite: ComponenteXPrenotazione) {
    if (ospite != null) {
      this.ospiti[index] = ospite;
      this.back.emit(this.ospiti);
    }
  }

  ngOnDestroy() {
    this.saveSubjects.forEach(x => x.complete());
  }
}