import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import moment from 'moment';
import { Subject, Subscription } from 'rxjs';
import { SessionService } from 'src/app/core/session.service';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { Albergo, ComponenteXPrenotazione, Comune, Nazione } from 'src/app/shared/models/bo.model';
import {
  AlbergoMySelf,
  AutocompleteDataLoader,
  DialogOptions,
  FileDto,
  GetNazioneTradottaRQ,
  GetNazioniTradotteRQ,
  setDatiCheckInRQ,
  WebApiRequestFilter,
  WebApiResponse,
} from 'src/app/shared/models/dto';
import { EnteRilascioDocumento, Sesso, TipoComponentePrenotazione, TipoDocumento } from 'src/app/shared/models/enum';
import { ApiService } from 'src/app/shared/services/api.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

import { MySelfCheckInAllegatoAddComponent } from './my-self-check-in-allegato-add/my-self-check-in-allegato-add.component';

@Component({
  selector: 'app-my-self-check-in',
  templateUrl: './my-self-check-in.component.html',
  styleUrls: ['./my-self-check-in.component.scss']
})
export class MySelfCheckInComponent extends BaseComponent implements OnInit, OnDestroy {

  @Input() albergo: AlbergoMySelf;
  @Input() model: ComponenteXPrenotazione;
  @Input() isVisualizzaTuttiDatiSelfCheckIn: boolean;
  @Input() isBloccaModificaDatiCheckIn: boolean;
  @Input() roomingList: boolean = false;
  @Input() saveSubject: Subject<void>;
  @Input() lingua: string;
  @Input() orarioDiArrivoMinimo: string;
  @Input() orarioDiArrivoMassimo: string;

  @Output() back: EventEmitter<ComponenteXPrenotazione> = new EventEmitter();
  @Output() formValidChange: EventEmitter<boolean> = new EventEmitter();
  @Output() saveEnabledChange: EventEmitter<boolean> = new EventEmitter();

  public enumSesso = Sesso;
  public enumTipoComponentePrenotazione = TipoComponentePrenotazione;
  public enumTipoDocumento = TipoDocumento;
  public enumEnteRilascioDocumento = EnteRilascioDocumento;
  public selectedComune: Comune = new Comune();
  public selectedComuneNascita: Comune = new Comune();
  public selectedComuneRilascioDocumento: Comune = new Comune();
  public dataLoaderComune: AutocompleteDataLoader;
  public dataLoaderComuneNascita: AutocompleteDataLoader;
  public dataLoaderComuneRilascioDocumento: AutocompleteDataLoader;
  public formValid: boolean = false;
  public saveEnabled: boolean;
  public getNazioniTradotteRQ = new GetNazioniTradotteRQ();
  public getNazioniTradotteRilascioDocumentoRQ = new GetNazioniTradotteRQ();
  public nazioneItalia: Nazione;
  public sub: Subscription;

  public dataNascitaMinDate: string = this.getDate(moment().subtract(100, 'years'));
  public dataNascitaMaxDate: string = this.getDate(moment());

  public dataRilascioDocumentoMinDate: string = this.getDate(moment().subtract(20, 'years'));
  public dataRilascioDocumentoMaxDate: string = this.getDate(moment());

  public dataScadenzaDocumentoMinDate: string = this.getDate(moment());
  public dataScadenzaDocumentoMaxDate: string = this.getDate(moment().add(20, 'years'));

  constructor(public apiService: ApiService,
    private sessionService: SessionService,
    public utilityService: UtilityService) {
    super();
  }

  ngOnInit() {
    if (this.model.comune != null && this.model.comune.oid > 0) {
      this.selectedComune = Object.assign({}, this.model.comune);
    }

    if (this.model.comuneNascita != null && this.model.comuneNascita.oid > 0) {
      this.selectedComuneNascita = Object.assign({}, this.model.comuneNascita);
    }

    if (this.model.comuneRilascioDocumento != null && this.model.comuneRilascioDocumento.oid > 0) {
      this.selectedComuneRilascioDocumento = Object.assign({}, this.model.comuneRilascioDocumento);
    }

    if (this.utilityService.isNullDate(this.model.dataNascita)) {
      this.model.dataNascita = null;
    }

    if (this.utilityService.isNullDate(this.model.dataRilascioDocumento)) {
      this.model.dataRilascioDocumento = null;
    }

    if (this.utilityService.isNullDate(this.model.dataScadenzaDocumento)) {
      this.model.dataScadenzaDocumento = null;
    }

    this.checkFormValidity();

    if (this.saveSubject != null) {
      this.sub = this.saveSubject.subscribe(() => this.save(false));
    }

    this.saveEnabled = !this.isBloccaModificaDatiCheckIn || !this.model.isCheckInOnlineEseguito || !this.formValid;

    this.saveEnabledChange.emit(this.saveEnabled);

    this.getNazioniTradotteRQ.addEmptyItem = false;
    this.getNazioniTradotteRQ.lingua = this.lingua;

    this.getNazioniTradotteRilascioDocumentoRQ.addEmptyItem = !this.model.isIntestatarioPrenotazione;
    this.getNazioniTradotteRilascioDocumentoRQ.lingua = this.lingua;

    let getNazioneItaliaRQ = new GetNazioneTradottaRQ();
    getNazioneItaliaRQ.nomeItaliano = 'ITALIA';
    getNazioneItaliaRQ.lingua = this.lingua;

    this.apiService.getNazioneTradotta(getNazioneItaliaRQ).subscribe((res: WebApiResponse) => {
      this.nazioneItalia = res.data;
    });
  }

  save(message: boolean = true) {
    if (this.formValid && this.saveEnabled) {
      this.model.dataNascita = this.getDate(this.model.dataNascita);
      this.model.dataRilascioDocumento = this.getDate(this.model.dataRilascioDocumento);
      this.model.dataScadenzaDocumento = this.getDate(this.model.dataScadenzaDocumento);
      this.model.isCheckInOnlineEseguito = true;
      this.model.isAggiornaSuWelcome = true;

      if (this.albergo == null) {
        this.model.albergo = this.sessionService.getAlbergo();
      } else {
        this.model.albergo = new Albergo(this.albergo.oid);
      }

      this.setIsLoading(true);

      let rq = new setDatiCheckInRQ();
      rq.componente = this.model;
      rq.lingua = this.lingua;

      this.apiService.setDatiCheckIn(rq).subscribe((rs: WebApiResponse) => {
        this.setIsLoading(false);

        if (message) {
          this.showInfo('globals.messages.datiSalvati').then(() => {
            this.back.emit(rs.data);
          });
        } else {
          this.back.emit(rs.data);
        }

        if (this.isBloccaModificaDatiCheckIn) {
          this.saveEnabled = false;
          this.saveEnabledChange.emit(this.saveEnabled);
        }
      });
    }
  }

  checkFormValidity() {
    this.formValid = this.model.isConsensoPrivacy && !!this.model.cognome && !!this.model.nome && this.model.nazioneNascita != null && this.model.nazioneNascita.oid > 0 && !this.utilityService.isNullDate(this.model.dataNascita) && (!this.model.nazioneNascita.isItalia || (this.model.comuneNascita != null && this.model.comuneNascita.oid > 0)) &&
      ((!this.isVisualizzaTuttiDatiSelfCheckIn && !this.model.isIntestatarioPrenotazione) || (this.model.nazione != null && this.model.nazione.oid > 0 && (!this.model.nazione.isItalia || (this.model.comune != null && this.model.comune.oid > 0)))) &&
      (!this.model.isIntestatarioPrenotazione || (this.model.tipoDocumentoEnum != null && !!this.model.numeroDocumento && !this.utilityService.isNullDate(this.model.dataRilascioDocumento) &&
        !this.utilityService.isNullDate(this.model.dataScadenzaDocumento) && this.model.enteRilascioDocumentoEnum != null && this.model.nazioneRilascioDocumento != null && this.model.nazioneRilascioDocumento.oid > 0 &&
        (!this.model.nazioneRilascioDocumento.isItalia || (this.model.comuneRilascioDocumento != null && this.model.comuneRilascioDocumento.oid > 0)) && !!this.model.cellulare)) &&
      (!this.model.email || this.utilityService.validateEmail(this.model.email));

    this.formValidChange.emit(this.formValid);
  }

  onUpdateOptionsComune(rq: WebApiRequestFilter) {
    this.dataLoaderComune = new AutocompleteDataLoader(this.apiService.getComuniFilter(rq));
  }

  onUpdateOptionsComuneNascita(rq: WebApiRequestFilter) {
    this.dataLoaderComuneNascita = new AutocompleteDataLoader(this.apiService.getComuniFilter(rq));
  }

  onUpdateOptionsComuneRilascioDocumento(rq: WebApiRequestFilter) {
    this.dataLoaderComuneRilascioDocumento = new AutocompleteDataLoader(this.apiService.getComuniFilter(rq));
  }

  onOptionSelectedComune(comune: Comune) {
    this.model.comune = comune;

    if (comune?.oid > 0) {
      this.model.nazione = this.nazioneItalia;
    } else {
      this.model.nazione = null;
    }

    this.checkFormValidity();
  }

  onOptionSelectedComuneNascita(comune: Comune) {
    this.model.comuneNascita = comune;

    if (comune?.oid > 0) {
      this.model.nazioneNascita = this.nazioneItalia;
    } else {
      this.model.nazioneNascita = null;
    }

    this.checkFormValidity();
  }

  onOptionSelectedComuneRilascioDocumento(comune: Comune) {
    this.model.comuneRilascioDocumento = comune;

    if (comune?.oid > 0) {
      this.model.nazioneRilascioDocumento = this.nazioneItalia;
    } else {
      this.model.nazioneRilascioDocumento = null;
    }

    this.checkFormValidity();
  }

  addAllegato() {
    let dialogOptions = new DialogOptions();
    dialogOptions.width = "50vw";

    let ref = this.showModalDialog(MySelfCheckInAllegatoAddComponent, dialogOptions);
    let component = <MySelfCheckInAllegatoAddComponent>ref.componentInstance;

    component.dialogRef = ref;

    ref.afterClosed().subscribe((x: FileDto) => {
      if (x != null) {
        this.model.allegati.push(x);
      }
    });
  }

  deleteAllegato(index: number) {
    this.showDialogConfirm('myself.eliminareAllegato')
      .then(si => {
        this.model.allegati.splice(index, 1);
      },
        no => { }
      );
  }

  dataNascitaChange() {
    if (!!this.model.dataNascita) {
      let dataNascita = moment(this.model.dataNascita).startOf('day');

      if (dataNascita.isBefore(moment(this.dataNascitaMinDate)) || dataNascita.isAfter(moment(this.dataNascitaMaxDate))) {
        this.model.dataNascita = null;
        this.showError('myself.formatoDataNonValido');
      }
    }
  }

  dataRilascioDocumentoChange() {
    if (!!this.model.dataRilascioDocumento) {
      let dataRilascioDocumento = moment(this.model.dataRilascioDocumento).startOf('day');

      if (dataRilascioDocumento.isBefore(moment(this.dataRilascioDocumentoMinDate)) || dataRilascioDocumento.isAfter(moment(this.dataRilascioDocumentoMaxDate))) {
        this.model.dataRilascioDocumento = null;
        this.showError('myself.formatoDataNonValido');
      }
    }
  }

  dataScadenzaDocumentoChange() {
    if (!!this.model.dataScadenzaDocumento) {
      let dataScadenzaDocumento = moment(this.model.dataScadenzaDocumento).startOf('day');

      if (dataScadenzaDocumento.isBefore(moment(this.dataScadenzaDocumentoMinDate)) || dataScadenzaDocumento.isAfter(moment(this.dataScadenzaDocumentoMaxDate))) {
        this.model.dataScadenzaDocumento = null;
        this.showError('myself.formatoDataNonValido');
      }
    }
  }

  ngOnDestroy() {
    if (this.sub != null) {
      this.sub.unsubscribe();
    }
  }
}