import { Component, Input, OnInit } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import * as moment from 'moment';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { Cliente, Lingua, PortaleXAlbergo, Recensione, RecensioneXLingua } from 'src/app/shared/models/bo.model';
import { PortaleMySelf, RecensioneMySelf, SetRecensioniXLinguaRQ } from 'src/app/shared/models/dto';
import { ApiService } from 'src/app/shared/services/api.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

@Component({
  selector: 'app-my-self-nuova-recensione',
  templateUrl: './my-self-nuova-recensione.component.html',
  styleUrls: ['./my-self-nuova-recensione.component.scss']
})
export class MySelfNuovaRecensioneComponent extends BaseComponent implements OnInit {

  @Input() cliente: Cliente;
  @Input() portale: PortaleMySelf;
  @Input() dialogRef: MatDialogRef<{}, any>;

  public recensione: Recensione = new Recensione();
  public recensioneXLingua: RecensioneXLingua = new RecensioneXLingua();

  public mesi: string[] = [];

  constructor(private apiService: ApiService,
    public utility: UtilityService) {
    super();
  }

  ngOnInit() {
    this.recensione.portaleXAlbergo = new PortaleXAlbergo(this.portale.oid);
    this.recensione.isSelfCheckIn = true;
    this.recensione.cliente = this.cliente;
    this.recensione.voto = 3;
    this.recensioneXLingua.lingua = this.cliente.lingua != null ? this.cliente.lingua : new Lingua(1);

    let today = moment();
    today.set('date', 1);

    for (let i = 11; i >= 0; i--) {
      let date = moment(today);
      date.subtract(i, 'month');
      this.mesi.push(this.getDate(date));
    }
  }

  save() {
    this.setIsLoading(true);
    this.recensione.nome = this.recensioneXLingua.titolo
    this.recensione.data = this.getDate(moment());

    let rq: SetRecensioniXLinguaRQ = new SetRecensioniXLinguaRQ();
    rq.recensione = this.recensione;
    rq.recensioniXLingua = [this.recensioneXLingua];

    this.apiService.setRecensione(rq).subscribe(() => {
      this.setIsLoading(false);

      this.showInfo('myself.recensioneInviata').then(() => {
        let recensione = new RecensioneMySelf();
        recensione.data = this.recensione.data;
        recensione.titolo = this.recensioneXLingua.titolo;
        recensione.testo = this.recensioneXLingua.descrizione;
        recensione.voto = this.recensione.voto;
        this.dialogRef.close(recensione);
      });
    });
  }
}