import { Component, Input, OnInit } from '@angular/core';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { VoucherMySelf } from 'src/app/shared/models/dto';
import { LocalizationService } from 'src/app/shared/services/localization.service';

@Component({
  selector: 'app-my-self-voucher',
  templateUrl: './my-self-voucher.component.html',
  styleUrls: ['./my-self-voucher.component.scss']
})
export class MySelfVoucherComponent extends BaseComponent implements OnInit {

  @Input() voucher: VoucherMySelf;

  public promozioneValidaDalAl: string;
  public promozionePrenotabileDalAl: string;

  constructor(private localizationService: LocalizationService) {
    super();
  }

  ngOnInit() {
    if (!!this.voucher.validoDal || !!this.voucher.validoAl) {
      this.promozioneValidaDalAl = this.localizationService.getTranslationSync('myself.promozioneValidaDalAl', { dal: '<b>' + (!!this.voucher.validoDal ? this.voucher.validoDal : '---') + '</b>', al: '<b>' + (!!this.voucher.validoAl ? this.voucher.validoAl : '---') + '</b>' });
    }

    if (!!this.voucher.prenotazioneDal || !!this.voucher.prenotazioneAl) {
      this.promozionePrenotabileDalAl = this.localizationService.getTranslationSync('myself.promozionePrenotabileDalAl', { dal: '<b>' + (!!this.voucher.prenotazioneDal ? this.voucher.prenotazioneDal : '---') + '</b>', al: '<b>' + (!!this.voucher.prenotazioneAl ? this.voucher.prenotazioneAl : '---') + '</b>' });
    }
  }
}