import { Component, Input } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import * as detect from 'detect-file-type';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { FileDto } from 'src/app/shared/models/dto';

@Component({
  selector: 'app-my-self-check-in-allegato-add',
  templateUrl: './my-self-check-in-allegato-add.component.html',
  styleUrls: ['./my-self-check-in-allegato-add.component.scss']
})
export class MySelfCheckInAllegatoAddComponent extends BaseComponent {

  @Input() dialogRef: MatDialogRef<{}, any>;

  public maximumFileSize: number = 2000000;

  constructor() {
    super();
  }

  fileSelected(fileSelect: any) {
    let file: File = fileSelect.files == null || fileSelect.files.length == 0 ? null : fileSelect.files[0];

    if (file != null && (file.type == 'application/pdf' || file.type == 'image/png' || file.type == 'image/jpeg')) {
      if (file.size <= this.maximumFileSize) {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => {
          let base64 = reader.result.toString();

          if (!!base64) {
            let base64String = base64.slice(base64.indexOf(',') + 1);

            detect.fromBuffer(Uint8Array.from(atob(base64String), (c) => c.charCodeAt(0)), (err, result) => {
              if (!err && result != null && (result.mime == 'application/pdf' || result.mime == 'image/png' || result.mime == 'image/jpeg')) {
                let f = new FileDto();
                f.name = file.name;
                f.base64 = base64String;
                this.dialogRef.close(f);
              } else {
                fileSelect.value = '';
                this.showError('globals.messages.fileNonValido');
              }
            });
          } else {
            fileSelect.value = '';
            this.showError('globals.messages.fileNonValido');
          }
        }
      } else {
        fileSelect.value = '';
        this.showError('globals.messages.fileTroppoGrande', { size: Math.floor(this.maximumFileSize / 1000) });
      }
    } else {
      fileSelect.value = '';
      this.showError('globals.messages.fileNonValido');
    }
  }
}