import { Injectable } from '@angular/core';
import { AuthenticateMySelf, AuthenticateMySelfRQ, AuthenticateMySelfRS, Sessione } from 'src/app/shared/models/dto';
import { ApiService } from 'src/app/shared/services/api.service';
import { LocalizationService } from 'src/app/shared/services/localization.service';

import { LocalStorageService } from '../local-storage.service';
import { SessionService } from '../session.service';

@Injectable({
  providedIn: 'root'
})
export class AuthenticationService {

  constructor(private sessionService: SessionService,
    private localStorage: LocalStorageService,
    private localizationService: LocalizationService,
    private apiService: ApiService) { }

  loginMySelf(setLingua: boolean, oidAlbergo: number, oidPortale: number, email?: string, password?: string, cognome?: string, codicePrenotazione?: string, oid?: number) {
    return new Promise<AuthenticateMySelf>((resolve, reject) => {
      let rq = new AuthenticateMySelfRQ();
      rq.oidAlbergo = oidAlbergo;
      rq.oidPortale = oidPortale;
      rq.email = email;
      rq.password = password;
      rq.cognome = cognome;
      rq.codicePrenotazione = codicePrenotazione;
      rq.oid = oid;

      this.apiService.authenticateMySelf(rq).subscribe({
        next: (authenticateResponse: AuthenticateMySelfRS) => {
          if (authenticateResponse == null || authenticateResponse.data == null || authenticateResponse.data.clienteMySelf == null || authenticateResponse.data.albergo == null || !authenticateResponse.data.token || (authenticateResponse.errori && authenticateResponse.errori.length > 0)) {
            reject();
          } else {
            let sessione = new Sessione();
            sessione.albergo = authenticateResponse.data.albergo;
            sessione.clienteMySelf = authenticateResponse.data.clienteMySelf;
            sessione.token = authenticateResponse.data.token;
            sessione.isMySelf = true;

            this.sessionService.sessione = sessione;
            this.localStorage.set(this.sessionService.SESSION_KEY, sessione);

            if (setLingua && sessione.clienteMySelf != null && !!sessione.clienteMySelf.lingua) {
              let l = this.localizationService.languagesSBM.find(x => x.codice == sessione.clienteMySelf.lingua);

              if (l != null) {
                this.localizationService.setDefaultLang(sessione.clienteMySelf.lingua, true);
              } else {
                this.localizationService.setDefaultLang('it', true);
              }
            }

            resolve(authenticateResponse.data);
          }
        },
        error: () => reject()
      });
    });
  }
}